/* PARTIAL: SUBMIT BUTTON
-------------------------------------------- */

.ptf-submit-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  padding: 0;

  color: var(--text);
  font-size: px2rem(60px);
  font-weight: 700;

  border: none;
  background: none;

  transition: all var(--ptf-transition-duration) var(--ptf-transition-easing);
  cursor: pointer;

  svg {
    width: 0.7em;
    height: 0.7em;
    margin-top: 0.12em;
    transition: transform var(--ptf-transition-duration)
      var(--ptf-transition-easing);
  }
  span {
    margin-right: px2rem(40px);
  }

  &:hover {
    color: var(--ptf-accent-1);
  }
}

/* SECTION: STYLE 2
-------------------------------------------- */

.ptf-submit-button--style-2 {
  text-transform: uppercase;
  font-size: px2rem(24px);

  svg {
    margin-left: px2rem(20px);
  }
}

.ptf-login {
  background: RGB(250, 69, 41);
  width: px2rem(130px);
  height: px2rem(50px);
  border: none;
  border-radius: 12px;
  margin-left: px2rem(7px);
  color: white;
  font-size: px2rem(16px);
  padding: 0 !important;
  transition: all 0.2s ease-in-out;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.ptf-login--1 {
  background: RGB(250, 69, 41);
  width: px2rem(100px);
  height: px2rem(50px);
  border: none;
  border-radius: 12px;
  margin-left: px2rem(7px);
  color: white;
  font-size: px2rem(16px);
  padding: 0 !important;
  transition: all 0.2s ease-in-out;
}
.outlined-btn {
  padding: 0 20px;
  height: 48px;
  min-width: 120px;
  background-color: transparent;
  border: 1px solid var(--text);
  border-radius: 12px;
  color: var(--text);
  transition: all var(--ptf-transition-duration) var(--ptf-transition-easing);
  svg {
    margin-right: 10px;
  }
  &:hover {
    border-color: var(--ptf-accent-1);
  }
}
.dropdown-menu {
  z-index: 9999;
  .dropdown-item {
    color: black;
    &:hover {
      background: #fa4529;
      color: white;
    }
  }
}
