/* PARTIAL: MASK IMAGE
-------------------------------------------- */

.ptf-mask-image {
  position: relative;
  overflow: hidden;
  height: 500px;
  border-radius: 12px;
  object-fit: cover;
  border: none;
  img:not(:last-of-type) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: none;
    -webkit-mask-image: var(--ptf-mask-image);
    mask-image: var(--ptf-mask-image);
    mask-repeat: round;
  }
}
